@media(min-width:1483px) {
    #container {
        padding-top: 90px;
        padding-bottom: 60px;
    }
    
    #root {
        width: 1212px;
        left: calc((100vw - 1212px) / 2);
    }

    .grid_row:first-of-type {
        margin-bottom: 80px;
    }
    
    .contact_icon:not(:last-of-type) {
        margin-right: 34px;
    }
    
    #info_header {
        margin-bottom: 20px;
    }
    
    #keep_in_touch {
        margin-bottom: 20px;
    }
    
    #copyright_section {
        margin-top: 50px;
    }
}

